<template>
<modal v-model:visible="modalConfirmationVisible" modalWidth="26em">
  <template #content>
    <div class="modal-text">Побачивши відповідь, ви вже не зможете робити нових спроб. Все одно продовжити?</div>
  </template>
  <template #buttons>
    <button class="button-small" :style="{ background: colorTheme }" @click="skip()">Продовжити</button>
    <button class="button-small" :style="{ background: colorTheme }" @click="modalConfirmationVisible = false">Скасувати</button>
  </template>
</modal>

<div class="task-card">

  <div class="task-info">
    <i class="task-status material-icons" :style='{ background: task.completed ? mix3(task.percentage)+"cc" : "#0004" }'>
      {{ !task.completed ? "play_arrow" : task.percentage == 100 ? "done" : task.percentage == 0 ? "close" : "done" }}
    </i>
    <div>
      <span class="task-index">Завдання {{ taskIndex + 1 }} з {{ lesson.length }}</span>
      <div class="task-stats">
        <span>{{ task.attempts }}&thinsp;/&thinsp;{{ maxAttempts }} спроб</span>&ensp;&bull;&nbsp;
        <span>{{ task.completed ? task.score : 0 }}&thinsp;/&thinsp;{{ task.weight }} балів</span>
      </div>
    </div>
  </div>

  <div class="task-tip" v-if="task?.tip" v-show='showTip' :style="{ background: lighten(colorTheme,92) }">
    <div>
      <div v-html="task.tip.text.replace('\n<ul>','\n<br><ul>').replace('\n<ol>','\n<br><ol>')"></div>
      <br v-if="task.tip?.formula">
      <div v-if="task.tip?.formula" v-html='`$$${task.tip.formula}$$`'></div>
      <div v-if="task.tip?.note">
        <br/>
        <span class="tag1" style="background: orange">Зверніть увагу</span>&ensp;
        <span v-html='task.tip.note'></span>
      </div>
    </div>
    <img v-if='task.tip?.image' :src='`${$cloudinary}images/${task._id}t.png`'>
  </div>

  <div class="task-section">

    <div class="task-text" v-html="task.text"></div>

    <div class="task-content">
      <div class="task-options" v-if='task.items'>
        <div v-for='(item, index) in task.items' :key='index' style="padding: .55em 0">
          <span class="task-letter">{{ index + 1 }}</span>
          <span class="task-option" v-if='item !== "image"'>{{ item }}</span>
          <img v-else :src='`${$cloudinary}images/${task._id}-${index + 1}.png`'>
        </div>
      </div>
      <div class="task-options" v-if='task.options'>
        <div v-for='(option, index) in task.options' :key='index' style="padding: .55em 0">
          <span class="task-letter">{{ String.fromCharCode(1040 + index) }}</span>
          <span class="task-option" v-if='option !== "image"'>{{ option }}</span>
          <img v-else :src='`${$cloudinary}images/${task._id}${String.fromCharCode(97 + index)}.png`'>
        </div>
      </div>
      <img class="task-image" v-if='task.image' :src='`${$cloudinary}images/${task._id}.png`'>
    </div>

    <div class="task-panel" :style="{ display: ((task.completed && task.percentage==100) || task.attempts==maxAttempts) ? 'flex' : 'block' }">
      <div class="task-blank">
        <answer-blank ref='answerBlank'
          :type='task.type'
          :disabled='task.completed' 
          v-model:data='task.answer' 
          :correctData='task.completed ? task.key : undefined' 
          :showCorrect="task.attempts == maxAttempts">
          <template v-for="i in task.key.length" v-slot:[`label${i}`]>
            <div class="bar-wrapper" 
              v-if="task.type=='radio' && ((task.completed && task.percentage==100) || task.attempts==maxAttempts) && task.distribution[i-1].some(e => e>0)">
              <chart-column :data="task.distribution[i-1]" :colors="metricsColors(i-1)" :gap="5"/>
            </div>
          </template>
        </answer-blank>
        <div v-if='task.completed && task.attempts > 0 && task.attempts < maxAttempts && task.percentage < 100' @click="modalConfirmationVisible = true" style='margin-top: .7em'>
          <span class='input-show-correct'>Показати правильну відповідь</span>
        </div>
        <div class="show-comment" v-if="task?.comment && !showComment && ((task.completed && task.percentage==100) || task.attempts==maxAttempts)" @click="showComment = true">
          Натисніть, щоб побачити розв'язок
        </div>
      </div>

      <div class="task-buttons">
        <button class='button-plain' v-if="task.completed && task.percentage<100 && task.attempts<maxAttempts" @click="() => { task.completed = false }">Нова спроба</button>
        <button class='button-plain' v-if="!task.completed" :disabled="answerDisabled" @click='answer()'>Відповісти</button>
        <slot/>
      </div>

    </div>
    
    <div class="task-comment" v-if="task?.comment" v-show="showComment">
      <b>Розв'язок:</b>
      <span v-html="task.comment?.text"></span>
      <img v-if='task.comment?.image' :src='`${$cloudinary}images/${task._id}s.png`'>
    </div>

  </div>

</div>
</template>

<script>
import controller from '@/api/controller'
import modal from '@/components/modals/modal-basic.vue'
import answerBlank from '@/components/answer-blank.vue'
import chartColumn from '../components/charts/chart-column.vue'
export default {
  components: { modal, answerBlank, chartColumn },
  props: {
    taskIndex: { type: Number, required: true, default: 0 },
    maxAttempts: { type: Number, required: false, default: 2 },
    showTip: { type: Boolean, required: false, default: true },
  }, 
  emits: [ "answer", "skip" ],
  data() { return {
    showComment: false,
    modalConfirmationVisible: false,
    colorTheme: localStorage.getItem('colorTheme'),
  }},
  computed: {
    lesson() { return this.$store.state.tasks },
    task() { return this.lesson[this.taskIndex] },
    answerDisabled() { return !this.task.answered || this.$refs.answerBlank.v$.$invalid },
    tipText() { return this.task.tip.text.re }
  },
  watch: {
    taskIndex: function(val) {
      this.showComment = false
      this.$nextTick( _ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]) )
    },
  },
  methods: {
    metricsColors(i) { return ['А','Б','В','Г','Д'].map(e => e == this.task.key[i] ? this.$green : e == this.task.answer[i] ? this.$red : '#0004') },
    answer() {
      this.$emit('answer', {
        taskId: this.task._id,
        answer: this.task.answer,
        score: this.task.score,
        scoreMax: this.task.weight
      })
      this.task.completed = true
      this.task.attempts ++
    },
    skip() {
      this.$emit('skip', this.task._id)
      this.task.completed = true
      this.task.attempts ++
      this.modalConfirmationVisible = false
    }
  }
}
</script>

<style lang="scss">
//* {border: 1px solid red}
.task-card {
  width: min(75em,100%);
  margin: auto;
  background: white;
  box-shadow: 0 2px 8px rgba(0,0,0,.12);
  border-radius: 8px;
}

.task-section {
  padding: 1.75em 2em 2.5em 2em;
  margin-bottom: 3em;
  box-sizing: border-box;
}

.task-info {
  position: relative;
  z-index: 1;
  padding: 1.75em 2em;
  display: flex;
  align-items: center;
  column-gap: 1.25em;
  background: #00000005;
  border-bottom: 2px solid #0002;
  //box-shadow: 0 2px 6px -2px #0004;
  .task-status {
    width: 1.85em;
    height: 1.85em;
    line-height: 1.85em;
    border-radius: 25%;
    font-size: 1.75em;
    text-align: center;
    color: #fff;
    box-shadow: 0 1px 4px #0003;
  }
  .task-index { 
    font-weight: bold; 
    font-size: 1.25em;
  }
  .task-stats {
    * { display: inline-block }
    margin-top: 7px;
    line-height: 1.5em;
    font-size: .9em;
    color: #0008;
  }
}

.task-tip {
  padding: 1.75em 2em;
  display: flex;
  gap: 1em;
  align-items: center;
  justify-content: center;
  line-height: 1.5em;
  @media (max-width: 640px) { flex-wrap: wrap }
  div { flex-grow: 1 }
  img {
    flex-grow: 0;
    width: 9.5em;
    height: 9.5em;
    object-fit: contain;
  }
}

.task-text { 
  margin-bottom: 20px;
  line-height: 1.5em; 
}

.task-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em;
  .task-image {
    flex-grow: 0;
    height: 12em;
    object-fit: contain;
    margin-top: 10px;
  }
  .task-options { 
    flex-grow: 1 
  }
  .task-option {
    line-height: 1.5em;
  }
  .task-letter {
    margin-left: 1ch;
    margin-right: 2ch;
    font-size: 1.1em;
    font-weight: bold;
    vertical-align: middle;
  }
  .task-image {
    width: 14em;
    height: 14em;
    object-fit: contain;
  }
  img {
    margin: 10px 0;
    vertical-align: middle;
  }
}

.task-panel {
  flex-wrap: wrap;
  align-items: bottom;
  padding-top: 1.5em;
  .task-buttons {
    align-items: flex-end;
  }
  .task-blank { 
    margin-right: auto;
  }
  .bar-wrapper {
    height: 3em;
    margin: .5em 0;
  }
  .show-correct {
    margin-top: .5em;
    font-size: .9em;
    &:hover { opacity: .5; }
  }
}

.show-comment {
  margin-top: .7em;
  font-size: .85em;
  color: #000a;
  &:hover { text-decoration: underline; }
}
.task-comment {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
  margin-top: 1.5em;
  padding-top: 1.5em;
  line-height: 1.5em;
  border-top: 1px solid #0002;
  span { flex-grow: 1; }
  img {
    flex-basis: 100%;
    width: 11em;
    height: 11em;
    object-fit: contain;
  }
}

.task-buttons {
  gap: 15px;
  display: flex;
  margin-top: 1.5em;
  align-items: flex-end;
  @media (max-width: 639px) { 
    flex-basis: 100%;
    align-items: stretch;
  }
  *:first-child { margin-right: auto; }
  * { @media (max-width: 640px) { flex-basis: 100%; } }
}

.input-show-correct {
  font-size: .85em;
  &:hover { text-decoration: underline; }
}
</style>